.s-workshops-list {
  padding: clamp(50px, 5vw, 100px) 0;
  background: $black;

  .container {
    max-width: 1190px; }

  &__top {
    display: flex;
    justify-content: space-between;
    margin-bottom: clamp(24px, 4vw, 42px);

    @include sm {
      flex-direction: column; }

    .section {
      &__title {
        text-align: left;
        line-height: 1.32;
        font-weight: 900;
        font-size: clamp(32px, 5vw, 48px);
        margin-bottom: clamp(42px, 5vw, 62px); }

      &__subtitle {
        max-width: none;
        text-align: left;
        line-height: 1.32;
        margin: 24px 0;
        font-weight: 400;
        font-size: clamp(14px, 2vw, 16px);
        opacity: 0.6; }

      &__info {
        max-width: none;
        text-align: left;
        line-height: 1.32;
        margin: 32px 0;
        font-weight: 600;
        font-size: clamp(16px, 3vw, 20px);

        p {
          &:not(:first-child) {
            margin: clamp(16px, 3vw, 30px) 0 0; } }

        a {
          font-weight: 600;
          background: linear-gradient(90deg, #B974A0 0%, #7D7DC3 32.65%, #6BA7BE 62.24%, #A1AF7E 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent; } } } }


  &__desc {
    max-width: 450px;
    width: 100%;
    flex: 1 1 auto; }

  &__img {
    max-width: 540px;
    width: 100%;
    flex: 1 1 auto;
    margin-top: 130px;
    margin-left: 40px;

    @include sm {
      max-width: 520px;
      margin: 0 auto; } } }

.workshops-imgs {
  position: relative;
  width: 100%;
  display: grid;
  flex-wrap: wrap;
  grid-template-columns: 1fr 1fr;
  gap: 30px;

  @include md {
    gap: 20px; }

  @include sm {
    gap: 10px; }

  &__item {
    position: relative;
    aspect-ratio: 1.84;

    img {
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
      border-radius: 15px; } } }

.workshops-list {

  &__item {
    position: relative;
    display: flex;
    padding: 18px 0 20px;

    &:first-child {
      padding-top: 40px; }

    &:after {
      content: '';
      position: absolute;
      left: -1000px;
      right: -1000px;
      top: 0;
      border-top: 1px solid $gray2; }

    &:last-child {
      &:before {
        content: '';
        position: absolute;
        left: -1000px;
        right: -1000px;
        bottom: 0;
        border-top: 1px solid $gray2; } }


    @include sm {
      flex-direction: column; } }

  &__item-overlay {
    display: flex;
    width: 100%;
    cursor: pointer;
    text-decoration: none;
    color: #fff;

    @include sm {
      flex-direction: column; }

    &:hover {
      .workshops-list__title {
        text-decoration: underline !important; } } }

  &__type {
    flex: 0 0 42px;
    color: $gray3;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 500;
    margin-right: 20px;

    @include sm {
      flex: 1 1 auto;
      margin: 0 0 10px; }

    &.free {
      font-weight: 600;
      background: linear-gradient(90deg, #B974A0 0%, #7D7DC3 32.65%, #6BA7BE 62.24%, #A1AF7E 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; } }

  &__info {
    flex: 1 1;
    padding-right: 20px; }

  &__title {
    font-size: clamp(18px, 2.6vw, 20px);
    font-weight: 700;
    line-height: 1.32;
    text-transform: uppercase;
    color: $white;
    margin: -2px 0 0; }

  &__desc {
    display: none; }

  &__date {
    font-size: 12px;
    margin: 10px 0 0; }

  &__speakers {
    display: flex;
    flex-direction: column;
    flex: 0 0 200px;
    gap: 5px;
    padding-right: 20px;

    @include sm {
      flex-direction: row;
      flex: 1 1 auto;
      margin: 18px 0;
      gap: 20px 20px; } }

  &__speaker {
    display: flex;
    align-items: center; }

  &__speaker-photo {
    width: 40px;
    height: 40px;
    border-radius: 4px;
    margin: 0 12px 0 0; }

  &__speaker-name {
    font-size: 12px; }

  &__buttons {
    flex: 0 0 160px;
    display: flex;
    flex-direction: column;
    gap: 5px;

    .btn {
      @extend .btn--border;
      padding: 3px 15px;
      min-height: 52px;
      font-size: 12px;

      &:last-child {
        margin-bottom: -10px; } }

    @include sm {
      flex: 1 1 auto;
      flex-direction: row;
      flex: 1 1 auto;
      margin: 0;
      gap: 10px; } } }
