.hero {
	position: relative;
	min-height: 100vh;
	//min-height: calc(var(--vh, 1vh) * 100)
	padding: 30px 0 80px;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	//background-image: url(../img/hero-overlay.png)
	background-color: #1e2220;
	z-index: 1;
	pointer-events: none;

	&.hero_teams {
		position: relative;
		padding-top: 33%;
		min-height: 0;
		background: url(../img/hero.png) center top no-repeat;
		background-size: cover;
		margin-bottom: -20%;
		z-index: -1;

		@include md {
			margin-top: 80px; }

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: linear-gradient(180deg, rgba(18, 18, 18, 0) 0%, $mainBg 90%); } }


	&.hero_inner {
		min-height: 0;
		background: url(/img/h-bg.svg) right bottom no-repeat;
		background-color: #181818; }

	// &:after
	//ontent: ''
	//include coverdiv
	//-index: 1
	//ackground-color: #000
	//pacity: 0.35
	// &:before
	// 	content: ''
	// 	+coverdiv
	// 	z-index: 1
	// 	background-image: url(../img/hero-overlay.png)
	// 	background-position: center
	// 	background-repeat: no-repeat
	// 	background-size: cover
	@include tablet {
		padding: 45px 0 45px; }
	@include md {
		padding: 60px 0 60px; }
	@include sm {
		padding: 20px 0 20px; }
	@include r(380) {
		@include rhmin(600) {
			padding: 20px 0 40px!important;
			.hero__logo, .h-list {
				margin-bottom: 25px; } }

		.hero {
			&__title {
				font-size: clamp(32px, 5vw, 64px); } } }

	&__video {
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		// top: 50%
		// left: 50%
		// transform: translate(-50%, -50%)
		// min-width: 100%
		// min-height: 100%
		// width: auto
		// height: auto
		z-index: -1;
		filter: brightness(0.7);
		object-fit: cover;
		display: none;

		@include sm {
			display: none; } }

	&__bg {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 100%;
		min-height: 100%;
		width: auto;
		height: auto;
		z-index: -1;
		background: url(../img/hero.jpg) center center no-repeat;
		background-size: cover;
		filter: brightness(0.7);
		display: none; }

	&__inner {
		max-width: 1600px;
		//margin: 0 auto
		padding: 0 40px;
		z-index: 2;
		pointer-events: none;
		@include r(1460) {
			padding: 0 40px; }
		@include r(1279) {
			padding: 0 40px; }
		@include sm {
			padding: 0 20px; }
		@include xs {
			padding: 0 15px; } }

	&__logo {
		width: 80px;
		margin: 0 auto clamp(24px, 5vw, 58px);

		@media screen and (max-height: 768px) {
			margin: 0 auto 24px; } }


	&__suptitle {
		text-transform: uppercase;
		font-size: clamp(14px, 3vw, 20px);
		font-weight: 600;
		margin-bottom: clamp(20px, 4vw, 32px);
		text-align: center;

		p {
			&:not(:first-child) {
				margin-top: clamp(10px, 1.2vw, 18px); } } }

	&__title {
		font-size: clamp(36px, 6vw, 100px);
		line-height: 1.06;
		text-align: center;
		margin: 0 auto;
		margin-bottom: clamp(18px, 4vw, 34px);
		font-weight: 800;

		strong {
			font-weight: 800;
			background: linear-gradient(90deg, #B974A0 0%, #7D7DC3 32.65%, #6BA7BE 62.24%, #A1AF7E 100%);
			-webkit-background-clip: text;
			-webkit-text-fill-color: transparent; }


		@media screen and (max-height: 768px) {
			margin-bottom: 20px; }

		@include tablet {
			margin-bottom: 20px;
			margin-left: -30px;
			margin-right: -30px; }
		@include md {
			margin-bottom: 20px; }
		@include sm {
			margin-bottom: 20px;
			margin-left: 0;
			margin-right: 0; }
		@include xs {
			margin: 0 auto 20px;
			max-width: calc(100% - 5px); }

		@media screen and (max-height: 768px) {
			max-width: 1024px;
			font-size: clamp(48px, 7vw, 76px); } }

	&__btn {
		//max-width: 283px
		//margin: 0 auto
		display: flex;
		justify-content: center;
		flex-wrap: wrap;

		.btn {
			pointer-events: auto;

			&:not(:first-child) {
				margin: 0 0 0 20px; } }

		@include sm {
			flex-direction: column;

			.btn {
				margin: 10px auto 0;

				&:not(:first-child) {
					margin: 10px auto 0; } } } }

	&__partner {
		position: absolute;
		right: 50%;
		transform: translateX(660px);
		top: 10px;
		font-size: 12px;
		line-height: 1.4;
		pointer-events: auto;
		z-index: 2;

		@include laptop {
			transform: translateX(0);
			right: 10px;
			top: 10px; }

		@include tablet {
			right: 20px; }

		@include md {
			display: flex;
			flex-direction: column;
			align-items: center;
			position: relative;
			right: 0;
			margin: 20px auto; } }

	&__partners {
		display: flex;
		margin-top: 4px;

		@include tablet {
			margin-top: 5px; } }

	&__partner-img {
		display: flex;
		align-items: center;
		justify-content: center;

		&:not(:first-child) {
			margin: 0 0 0 20px;

			@include sm {
				margin: 0 0 0 10px; } }

		img {
			height: 60px;
			width: auto; } }

	&__timer {
		display: none;
		position: absolute;
		padding: 0 4vw;
		right: 0;
		left: 0;
		bottom: 1vw;
		font-size: 15vw;
		font-weight: 600;
		color: $themeColor;
		opacity: .2;
		line-height: 1;
		white-space: nowrap;

		.live {
			text-align: center; } }

	&_live {
		align-items: flex-start;
		height: 150px;
		min-height: 0;
		padding: 20px 0;

		@include tablet {
			padding: 20px 0; }
		@include md {
			padding: 20px 0;
			height: 120px; }
		@include sm {
			padding: 10px 0; }
		@include xs {
			height: auto;
			padding: 10px 0 !important; }


		.container {
			position: relative;
			z-index: 2;
			pointer-events: auto;
			width: 100%;
			height: 100%;
			padding: 0 80px;

			@include sm {
				padding: 0 20px; } }

		.hero {
			&__bg {
				z-index: -1;
				background: url(../img/screens-done-op.svg) center center;
				background-size: 1060px;
				display: block; }

			&__inner {
				position: relative;
				width: 100%;
				height: 100%;
				padding: 0;
				margin: 0;
				display: flex;
				justify-content: space-between;

				@include md {
					margin: 0; }

				@include xs {
					flex-direction: column-reverse;
					align-items: center; } }

			&__date {
				font-size: 18px;
				margin: auto 0;

				@include md {
					font-size: 14px; }

				@include xs {
					font-size: 12px;
					margin: 15px 0 10px; } }

			&__logo {
				width: 80px;
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);
				margin: 0;

				@include md {
					width: 80px;
					margin: 0px auto 0; }

				@include xs {
					position: relative;
					left: 0;
					top: 0;
					transform: translate(0, 0);
					width: 80px;
					margin: 0px auto 0; } }

			&__partner {
				position: relative;
				height: auto;
				margin: auto 0 auto auto;
				top: 0;
				right: 0;

				@include tablet {
					font-size: 10px; }

				@include md {
					margin: auto 0 auto;

					img {
						height: 45px; } }

				@include xs {
					display: none; }

				img {
					height: 65px; } }

			&__partners {
				@include md {
					margin: 0; } }



			&__partner-text {
				@include sm {
					display: none; } } } }

	&_attendees {
		min-height: 0;
		padding: 45px 0;
		background-size: cover;
		background-position: center center;

		.hero {

			&__inner {
				width: 100%; }

			&__logo {
				width: 80px;
				margin-bottom: 40px;

				@include md {
					width: 80px; }

				@include xs {
					width: 80px; } }

			&__title {
				text-align: center; }

			&__suptitle {
				margin-bottom: 10px; } } } }



.h-list {
	display: flex;
	justify-content: space-between;
	max-width: 720px;
	margin: 0 auto clamp(26px, 4vw, 54px);
	padding: 0;

	@media screen and (max-height: 768px) {
		margin: 0 auto 26px; }

	&_centered {
		justify-content: center; }

	&_min {
		max-width: 411px; }
	@include tablet;

	@include md {
		max-width: 600px;
		padding: 0; }
	@include xs {
		flex-wrap: wrap;
		justify-content: center;
		padding: 0; }

	&__item {
		display: flex;
		flex-direction: column;

		@include md {
			flex-direction: column;
			text-align: center; }

		@include sm {
			width: 50%;
			align-items: center;
			text-align: center; }
		&:not(:last-child) {
			margin-right: 40px;
			@include sm {
				margin-right: 0; } }
		@include xs {
			margin-bottom: 9px; }
		&:nth-child(2n) {
			@include xs {
				margin-right: 0; } } }
	//+xs
	//argin-right: 0
	///margin-bottom: 20px
	//argin-bottom: 15px
	//&_sm
	//include xs
	//max-width: 90px
	//&_lg
	//include xs
	//width: auto
	//max-width: unset

	&__value {
		font-size: clamp(32px, 4.6vw, 48px);
		margin: 0 20px 0 0;
		@include bold;
		display: flex;
		align-items: center;
		@include md {
			margin: 0;
			justify-content: center; }
		@include sm {
			justify-content: flex-start; }
		@include xs {
			margin-bottom: 0;
			line-height: 1.1; }

		.icon-infinity {
			width: 71px;
			height: 60px;
			fill: #FFF; } }

	&__text {
		font-size: 14px;
		line-height: 1.4;
		padding: 10px 0;
		opacity: 0.4;
		&-top {
			font-size: 20px;
			@include medium;
			text-transform: uppercase;
			@include tablet {
				font-size: 16px; }
			@include xs {
				font-size: 14px; } }
		@include xs {
			font-size: 12px; }
		@include tablet {
			padding: 9px 0; }
		@include md {
			padding: 0; } }
	&__icon {
		margin-top: 10px;
		margin-left: 8px;
		font-size: 30px;
		&_anim {
			animation: heart 2s infinite; }
		.icon {
			fill: $white; }
		@include sm {
			margin-top: 6px;
			.icon {
				width: 0.9em; } } } }



@keyframes heart {
	0% {
		transform: scale(1); }
	50% {
		transform: scale(1.2); }
	100% {
		transform: scale(1); } }


.logo {
	display: block;
	width: 100%;
	pointer-events: auto;
	img {
		//height: 100%
		width: 100%;
		display: block;
		border-radius: 10px;
		box-shadow: 0 3px 8px rgba(0, 0, 0, 0.5); } }
