.s-free-ticket {
  padding: clamp(50px, 5vw, 64px) 0;

  .container {
    max-width: 1320px; }

  .section {
    &__title {
      line-height: 1.28;
      font-weight: 900;
      font-size: clamp(26px, 5vw, 60px);
      text-transform: uppercase;
      margin: 0 auto 26px;
      max-width: 800px;
      position: relative;
      z-index: 2; } } }


.free-ticket {

  &__content {
    display: flex;
    max-width: 1062px;
    margin: 0 auto;
    gap: 20px 76px;
    justify-content: space-between;

    @include md {
      gap: 20px 40px;
      flex-direction: column-reverse; } }

  &__text {
    display: flex;
    flex-direction: column;
    flex: 0 1 390px;
    justify-content: center;

    @include md {
      flex: 1 1 auto;
      text-align: center; }

    .btn {
      margin-top: 30px; } }

  &__descr {
    font-size: clamp(14px, 2vw, 20px);
    line-height: 1.5;
    font-weight: 500;

    em {
      font-weight: 600;
      font-style: normal;
      background: linear-gradient(90deg, #B974A0 0%, #7D7DC3 32.65%, #6BA7BE 62.24%, #A1AF7E 100%);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; }

    p {
      &:not(:first-child) {
        margin: 24px 0 0; } } }

  &__img {
    flex: 0 0 363px;

    // +md
    //flex: 0 0 300px
    //max-width: 300px

    @include md {
      flex: 0 0 auto;
      margin: 0 auto; }

    @include xs {
      max-width: 232px; }

    img {
      display: block;
      width: 100%;
      height: auto; } }

  &__form {
    display: flex;
    max-width: 974px;
    margin: 0 auto;

    @include md {
      flex-direction: column;
      max-width: 310px; } }

  &__input {
    @include regular;
    min-height: 52px;
    line-height: 1.24;
    flex: 1 1 auto;
    background: $white;
    padding: 14px 20px;
    font-size: 14px;
    display: block;

    &::placeholder {
      color: $black; }

    &:not(:first-child) {
      margin: 0 0 0 3px; }

    &:first-of-type {
      border-radius: 26px 0 0 26px; }

    &:last-of-type {
      border-radius: 0 26px 26px 0; }

    @include md {
      border-radius: 26px;

      &:not(:first-child) {
        margin: 10px 0 0 0; }

      &:first-of-type,
      &:last-of-type {
        border-radius: 26px; } } }

  &__submit {
    flex: 0 0 180px;
    margin: 0 0 0 12px;
    min-height: 52px;

    &:after,
    &:before {
      display: none; }

    @include md {
      margin: 30px auto 0;
      flex: 0 0 auto;
      mix-width: 180px; } } }


.badge-img {
  position: relative;
  z-index: 1;
  &:before {
    content: '';
    position: absolute;
    width: 244px;
    height: 136px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: linear-gradient(90deg, #EE6431 2.54%, #6565FF 21.28%, #A7A7FF 33.52%, #6DA4BF 44.52%, #FF9022 68.61%, #FFD0A8 80.79%, #FDAC5C 92.51%), #000000;
    opacity: 0.78;
    box-shadow: 0px 10px 250px #E38171;

    @include sm {
      width: 90px;
      height: 80px; } }

  &:after {
    content: '';
    position: absolute;
    right: 130px;
    top: -174px;
    height: 200px;
    width: 44px;
    background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%);
    z-index: -1;
    transform: skew(-32deg);
    border-radius: 0 0 2px 2px;

    @include sm {
      width: 24px;
      height: 124px;
      right: 66px;
      top: -108px;
      border-radius: 0 0 1px 1px; } }

  &__card {
    display: flex;
    align-items: center;
    position: relative;
    width: 448px;
    height: 250px;
    padding: 100px 62px 28px;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.2);
    box-shadow: 0px 10px 100px rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(40px);

    @include sm {
      width: 234px;
      height: 132px;
      padding-left: 62px;
      border-radius: 10px;
      padding: 64px 30px 30px; } }

  &__img {
    margin: 0 20px 0 0;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: 1px solid rgba(255, 255, 255, 0.3);
    display: flex;
    flex: 0 0 auto;
    align-items: center;
    justify-content: center;

    @include sm {
      width: 54px;
      height: 54px;
      margin: 0 12px 0 0; }

    .icon-face {
      width: 46px;
      height: 58px;
      fill: #fff;

      @include sm {
        width: 26px;
        height: 34px; } } }

  &__desc {

    h3 {
      @include bold;
      font-size: 32px;
      font-weight: 900;
      text-transform: uppercase;
      line-height: 1.28;
      margin: 0 0 2px;

      @include sm {
        font-size: 18px; } }

    p {
      font-size: 12px;
      margin: 8px 0 0;
      line-height: 0.9;

      @include sm {
        font-size: 8px;
        margin: 5px 0 0; } } }

  &__barcode {
    position: relative;
    width: 106px;
    height: 20px;
    background: url("data:image/svg+xml,%3Csvg version='1.1' id='Layer_1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink' x='0px' y='0px' width='106.5px' height='20px' viewBox='0 0 106.5 20' style='enable-background:new 0 0 106.5 20;' xml:space='preserve'%3E%3Cstyle type='text/css'%3E .st0%7Bfill:%23FFFFFF;%7D%0A%3C/style%3E%3Cg%3E%3Cpath class='st0' d='M0,0h20v6.7H0L0,0z'/%3E%3Cpath class='st0' d='M0,13.3h6.7V20H0L0,13.3z'/%3E%3Cpath class='st0' d='M6.7,6.7h6.7v6.7l-6.7,0L6.7,6.7z'/%3E%3Cpath class='st0' d='M13.3,13.3l6.7,0V20h-6.7L13.3,13.3z'/%3E%3C/g%3E%3Cg%3E%3Cpath class='st0' d='M27,20V0l3.5,0v20H27z'/%3E%3Cpath class='st0' d='M33.9,20V0l1.2,0v20H33.9z'/%3E%3Cpath class='st0' d='M36.2,20V0l2.3,0v20H36.2z'/%3E%3Cpath class='st0' d='M40.8,20V0L42,0v20H40.8z'/%3E%3Cpath class='st0' d='M43.1,20V0l1.2,0v20H43.1z'/%3E%3Cpath class='st0' d='M45.4,20V0l1.2,0v20H45.4z'/%3E%3Cpath class='st0' d='M47.7,20V0l3.5,0v20H47.7z'/%3E%3Cpath class='st0' d='M54.7,20V0l1.2,0v20H54.7z'/%3E%3Cpath class='st0' d='M57,20V0l2.3,0v20H57z'/%3E%3Cpath class='st0' d='M61.6,20V0l1.2,0v20H61.6z'/%3E%3Cpath class='st0' d='M63.9,20V0L65,0v20H63.9z'/%3E%3Cpath class='st0' d='M71.9,20V0l1.2,0v20H71.9z'/%3E%3Cpath class='st0' d='M74.2,20V0l3.5,0v20H74.2z'/%3E%3Cpath class='st0' d='M81.2,20V0l1.2,0v20H81.2z'/%3E%3Cpath class='st0' d='M83.5,20V0l2.3,0v20H83.5z'/%3E%3Cpath class='st0' d='M88.1,20V0l1.2,0v20H88.1z'/%3E%3Cpath class='st0' d='M90.4,20V0l1.2,0v20H90.4z'/%3E%3Cpath class='st0' d='M92.7,20V0l1.2,0v20H92.7z'/%3E%3Cpath class='st0' d='M95,20V0l3.5,0v20H95z'/%3E%3Cpath class='st0' d='M101.9,20V0l1.2,0v20H101.9z'/%3E%3Cpath class='st0' d='M104.2,20V0l2.3,0v20H104.2z'/%3E%3C/g%3E%3C/svg%3E") center center no-repeat;

    @include sm {
      width: 62px;
      height: 12px; } }

  &__stripe {
    position: absolute;
    width: 80px;
    height: 24px;
    border: 1px solid rgba(255, 255, 255, 0.3);
    left: 50%;
    top: 24px;
    transform: translateX(-50%);
    border-radius: 12px;
    background: $mainBg;

    @include sm {
      width: 40px;
      height: 12px;
      top: 12px; }

    &:before {
      content: '';
      position: absolute;
      left: -44px;
      bottom: 0px;
      height: 200px;
      width: 44px;
      background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%);
      transform: skew(32deg);
      border-radius: 0 0 2px 2px;

      @include sm {
        width: 24px;
        height: 124px;
        left: -36px;
        bottom: 5px;
        border-radius: 0 0 1px 1px; } } }

  &__head {
    font-size: 21px;
    line-height: 1;
    font-weight: 400;
    position: absolute;
    left: 22px;
    right: 22px;
    top: 0;
    height: 80px;
    padding: 24px 0 0;
    align-items: center;
    display: flex;
    justify-content: space-between;
    opacity: 0.7;

    &:after {
      content: '';
      position: absolute;
      right: 0;
      left: 0;
      bottom: -4px;
      height: 1px;
      background: rgba(255, 255, 255, 0.4);
      border-radius: 2px; }

    @include sm {
      left: 12px;
      right: 12px;
      padding: 0 5px;
      font-size: 12px;
      height: 34px;

      &:after {
        bottom: -2px; } }

    .icon {
      width: 10px;
      height: 10px;
      fill: $white;

      @include sm {
        width: 8px;
        height: 8px; } } } }
