.header {
	z-index: 9;
	background-color: $black;
	position: fixed;
	bottom: 0;
	left: 0;
	width: 100%;
	padding: 15px 0;
	@include md {
		bottom: auto;
		top: 0;
		padding: 0;
		pointer-events: none; }

	&__logo {
		display: none; }

	&__nav {
		@include md {
			width: 100%;
			margin: auto 0;
			padding: 20px 0; } }
	&__btn {
		position: absolute;
		top: 0;
		right: 20px;
		width: 186px;
		height: 100%;

		.btn {
			width: 100%;
			padding: 0;
			min-height: 0;
			height: 100%;

			@extend .btn--gradient;

			&:after,
			&:before {
				border-radius: 10px; } }

		@include tablet {
			width: 160px; }
		@include md {
			width: auto;
			position: relative;
			margin: 0 0 20px;
			display: none; } }

	&__alt-btn {
		display: none;
		position: fixed;
		bottom: 0;
		right: 0;
		width: 214px;
		z-index: 2;
		pointer-events: auto;
		transition: opacity ease 0.1s;

		.btn {
			width: 100%;
			height: 50px;
			border-radius: 0;
			padding: 0;

			&:after,
			&:before {
				border-radius: 0; } }

		@include md {
			width: auto;
			margin: 0;
			display: block;
			width: 160px;
			opacity: 0;
			visibility: hidden;
			display: none;

			&.visible {
				opacity: 1;
				visibility: visible; } } }

	&__socials {
		position: absolute;
		display: flex;
		align-items: center;
		flex: none;
		left: 20px;
		top: 0;
		height: 50px;


		.social {
			gap: 5px;
			&__item {
				&:not(:last-child) {
					margin: 0; } }

			&__link {
				width: 32px;
				height: 32px;
				display: flex;
				justify-content: center;
				align-items: center;
				border-radius: 10px;

				&:hover {
					background: $gray; }

				.icon {
					width: 24px;
					height: 24px;
					&-tiktok, &-instagram {
						transform: scale(0.8); } } } }

		@include md {
			order: 3;
			margin-top: 0;
			margin-bottom: 10px;
			position: relative;
			left: 0;
			top: 0;
			transform: none; } }

	&__inner {
		position: relative;
		display: flex;
		max-width: 1480px;
		padding: 0 20px;
		align-items: center;
		justify-content: center;
		min-height: 52px;
		margin: 0 auto;

		@include md {
			display: none;
			position: fixed;
			top: 0;
			left: 0;
			height: 100vh;
			height: calc(var(--vh, 1vh) * 100);
			height: 100%;
			padding: 1px 0;
			overflow: hidden;
			overflow-y: auto;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 100%;
			background-color: $gray;
			//transition: opacity 0.3s
			z-index: 3; }

		&.is-open {
			@include md {
				pointer-events: auto;
				display: flex; } } }

	&__burger {
		display: none;
		position: absolute;
		top: 10px;
		right: 10px;
		z-index: 4;
		@include md {
			display: inline-block;
			pointer-events: auto; } } }

.nav {
	display: flex;
	@include md {
		display: block; }
	&__link {
		text-decoration: none;
		color: $white;
		transition: 0.3s;
		font-size: 16px;
		height: 50px;
		display: flex;
		padding: 0 13px;
		align-items: center;
		justify-content: center;
		border-radius: 10px;

		&:hover {
			color: #9dde00;
			background: $gray; }

		@include md {
			font-size: 14px;
			display: flex;
			text-align: center;
			padding: 10px 20px;
			font-weight: 500; }

		.no-touch &:hover {
			color: $themeColor; } } }

.burger {
	width: 60px;
	height: 60px;
	cursor: pointer;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	@include sm {
		width: 50px;
		height: 50px; }

	span {
		transition: 0.3s;
		display: block;
		height: 4px;
		width: 36px;
		background-color: #ffffff;
		border-radius: 5px;
		&:not(:last-child) {
			margin-bottom: 6px; } }
	&.is-active {
		span {
			&:nth-child(1) {
				transform: translate(0, 10px) rotate(45deg); }
			&:nth-child(2) {
				transform: translateX(0);
				opacity: 0; }
			&:nth-child(3) {
				transform: translate(0, -10px) rotate(-45deg); } } } }


