.sponsors {
	padding: clamp(50px, 5vw, 100px) 0;
	background: linear-gradient(180deg, #000000 0%, #181818 90.72%);

	.container {
		max-width: 1390px; }

	.section__title {
		font-size: clamp(32px, 4vw, 64px);
		font-weight: 800;
		margin-bottom: clamp(24px, 3vw, 38px); }

	&__description {
		max-width: 640px;
		margin: 0 auto 62px;
		@include r(680) {
			margin-bottom: 42px; }
		//+r(480)
		//argin-bottom: 30px
		p {
			font-size: 20px;
			line-height: 1.34;
			text-align: center;

			&:not(:last-child) {
				margin-bottom: 18px;
				@include sm {
					margin-bottom: 5px; } }
			a {
				text-transform: none;
				font-weight: 700;
				background: linear-gradient(90deg, #B974A0 0%, #7D7DC3 32.65%, #6BA7BE 62.24%, #A1AF7E 100%);
				-webkit-background-clip: text;
				-webkit-text-fill-color: transparent; } } }

	&__offer {
		text-transform: uppercase;
		margin: clamp(32px, 4vw, 64px) 0 0;

		a {
			display: inline-flex;
			margin: 14px 0 0; } } }

.sponsors-block {
	padding: 20px 0;
	position: relative;

	&:after {
		content: '';
		position: absolute;
		top: 0;
		left: -1000px;
		right: -1000px;
		border-top: 1px solid $gray2;

		&:last-child {
			&:before {
				border-bottom: 1px solid $gray2; } } }

	&__item {
		text-align: center; }

	&_xl {
		.sponsors-block__item {
			max-width: 50%;
			width: 33.33%;
			padding: 25px 30px;
			@include tablet {
				padding: 20px;
				img {
					max-width: 100%; } }
			@include md {
				width: 50%; }
			@include sm {
				width: 75%;
				max-width: 100%;
				padding: 20px 10px; } } }
	&_lg {
		//.sponsors-block__list
		//argin: 0 -50px
		//include tablet
		//margin: 0 -20px
		//include xs
		//margin: 0 -10px
		.sponsors-block__item {
			padding: 25px 30px;
			max-width: 50%;
			width: 33.33%;
			img {
				max-width: 100%; }
			@include tablet {
				padding: 20px; }
			@include md {
				width: 50%; }

			@include xs {
				padding: 7px; }
 } }			//max-width: 217px
	&_md {
		.sponsors-block__list {
			margin: 0 -50px;
			@include tablet {
				margin: 0 -20px; }
			@include xs {
				margin: 0 -10px; } }
		.sponsors-block__item {
			padding: 25px 30px;
			width: 33.33%;
			img {
				max-width: 100%; }
			@include tablet {
				padding: 20px; }
			@include xs {
				padding: 5px; }
			//max-width: 193px
 } }			//width: 100%
	&_sm {
		.sponsors-block__list {
			margin: 0 -25px; }
		.sponsors-block__item {
			padding: 20px 25px;
			width: 25%;
			img {
				max-width: 100%; }
			@include tablet {
				padding: 20px; }

			@include md {
				width: 33.33%; }
			//+md
			//idth: 50%
			//mg
 } }			//max-width: 100%

	&_xs {
		.sponsors-block__list {
			margin: 0 -15px; }
		.sponsors-block__item {
			width: 20%;
			a {
				padding: 20px 25px; }
			img {
				max-width: 100%; }
			@include tablet {
				a {
					padding: 20px; } }

			@include md {
				width: 25%; }

			@include xs {
				width: 33.3%;
				a {
					padding: 15px 10px; } } } }

	&_auto {
		.sponsors-block__list {
			justify-content: space-between;
			margin: 0 -15px; }
		.sponsors-block__item {
			width: auto;
			a {
				padding: 20px 25px; }
			img {
				max-width: 100%; }
			&:last-child {
				margin: 0 auto 0 0; }
			@include tablet {
				a {
					padding: 20px; } }

			@include sm {
				width: 25%;
				a {
					padding: 15px 10px; } }

			@include xs {
				width: 33.3%;
				a {
					padding: 15px 10px; } } } }
	&__list {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-wrap: wrap; }

	&__item {

		@include tablet {
			display: flex;
			justify-content: center; }
		@include sm {
			max-width: 230px;
			img {
				max-width: 100%; } }
		a {
			display: flex;
			justify-content: center;
			align-items: center;
			transition: 0.3s;

			&:hover {
				opacity: 0.7; }

			img {
				transition: all ease 0.2s;
				transform: translateY(0);
				opacity: 1;
				height: auto; } } }

	&__title {
		font-size: 12px;
		font-weight: 600;
		margin-bottom: 20px;
		opacity: 0.4; } }
